.Toolbar{
    height: 56px;
    width: 100%;
    z-index: 90;
    position: fixed;
    top:0;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0 20px;
    background-color: #703B09;
}

.Toolbar nav{
    height: 100%;
}

.Logo{
    height: 80%;
}

@media (max-width: 499px) {
    .DesktopOnly{
        display: none;
    }
}


